<script>


// import Multiselect from  'vue-multiselect'
export default {
    components: { DynamicForm },
    props: {
        showlayoutpopup: {
            type: Boolean
        },
        CustomlayoutId: {
            type: Number,
            required: true
        },
        MODULEID: {
            type: Number,
            required: true
        },
         SUBMODULEID:{
            type: Number,
            required: true
         }, 
         LAYOUTNAME: {
            type: String,
            required: true
         }, 
         PLATFORM: {
            type: String,
            required: true
         }, 
         LAYOUTTYPE: {
            type: String,
            required: true
         }, 
         DESCRIPTION :{
            type: String,
            required: true
         }, 
         LAYOUTSHARETOID :{
            type: String,
            required: true
         }
    },
    data() {
        return {

            SharetoRole: [],
            shearedtypeRole: [],
            searchable: true,
            reqFrom: null,
            loggeduserid: '',
            companyId: '',
            pageSize: 10000,
            pageNum: 1,
            role_id: '',
            Options: [],
            Cloneheader: false,
            AssignRolePopup:false,
            RolesCheckData:[],
            RolesCheckReturnData:[],
            ShareToRoleitem:'',
            mode: 'tags',
            FormSchema: [{
            layoutType: "Single",
            Data: [{
            astype: "RadioField",
            label: 'Share with',
            name: "Sharewithtype",
            value: "User",
            placeholder: "",
            config: {
              options: [{
                value: "User",
                name: "User"
              },
              {
                value: "Role",
                name: "Role"
              }
              ],
              showAddIcon: false,
              onChange: this.SelectshareType
            },
              validationRules: "",
            },]
        },
        {
          layoutType: "double",
          Data: [{
            astype: "CustomSelectField",
            mode: "tag",
            label: this.$t('User '),
            name: "User",
            value: "",
            config: {
            options: [],
            showAddIcon: false,
            onChange: this.GetUSerId
            },
            placeholder: this.$t('SelectUser'),
            validationRules: { "required": true, max: 100 },
            },
            {
            astype: "CustomSelectField",
            mode: "tag",
            label: this.$t('Role '),
            name: "Role",
            value: "",
            config: {
            options: [],
            showAddIcon: false,
            onChange: this.GetRolesID
            },
            placeholder: this.$t('SelectUser'),
            visibility: false,
            validationRules: { "required": true, max: 100 },
            }],
            }
            ],
            buttons: [{
            type: "submit",
            class: "btn btn-success",
            text: "<i class='fa fa-save pr-2'></i>Save",
            },
            {
            type: "button",
            class: "btn btn-danger",
            text: "<i class='fa fa-close pr-2'></i>Cancel",
            onClick: this.closepopup
            }],
            AssignRolePopup: false,
            shareroleids:'',
            companyId: '',
            pageSize: 10000,
            RolesCheckData:[],
            sharedtoUser:true,
            sharedtoUserIds:[],
            sharedtoRoleIds:[]
        }
    },
    created() {
        var vm = this;
        vm.loggeduserid = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.FetchRoleData();
        //vm.FetchClone();
        vm.FetchData();
    },
    methods: {
        closeAssignpopup() {
            var vm = this;
            vm.AssignRolePopup = false;           
        },
        roleListingDelete: function (e) {
            var vm = this;
            vm.AssignRolePopup = false;
            vm.SharetoRole.pop()
        },
        // onChangeOnDropdownChange: function () {
        //     var vm = this;
        //     var lastItem = vm.SharetoRole.length-1;
        //     vm.ShareToRoleitem=  vm.SharetoRole[lastItem].value;
