        //     DataService.CheckRolesOnCustomLayout(params).then(function (response) {
        //         vm.RolesCheckReturnData = response.data.data;
        //         if (response.data.data != '' && response.data.data != null) {
        //             vm.RolesCheckData = [];
        //             vm.RolesCheckReturnData[0].forEach((item, index) => {
        //                 vm.RolesCheckData.push({
